import * as React from 'react';
import './styles.scss';
// import logo from '../../../../../public/intellicare.png'
import logo from './logo';

const tableStyle = {
    width: "100%",
    // marginLeft: "1.5em",
}

const tdStyle = {
    fontSize: "0.85em",
    border: "1px solid #000",
    marginTop: "0",
    marginBottom: "0",
    marginRight: "0",
    marginLeft: "0",
    wordWrap: "break-word",
    verticalAlign: "center"
}

const thStyle = {
    fontSize: "0.9em",
    backgroundColor: "#ccc",
    border: "1px solid #000",
    marginTop: "0",
    marginBottom: "0",
    marginRight: "0",
    marginLeft: "0",
    wordWrap: "break-word",
    verticalAlign: "center"
}

const spanStyle = {
    display: "block",
    borderBottom: "1px solid #000",
    wordWrap: "break-word",
    padding: "5px 10px",
    marginTop: "0",
    marginBottom: "0",
    marginRight: "0",
    marginLeft: "0",
}

const globals = {
    alignCenter: {
        textAlign: "center",
    },
    alignLeft: {
        textAlign: "left",
    },
    removeAllMargin: {
        marginTop: "0px",
        marginBottom: "0px",
        marginRight: "0px",
        marginLeft: "0px",
        paddingLeft: "0px",
        paddingBottom: "0px",
        paddingRight: "0px",
        paddingTop: "0px"
    },
    borderBottomNone: {
        borderBottom: "none",
        padding: "5px 10px",
        marginTop: "0",
        marginBottom: "0",
        marginRight: "0",
        marginLeft: "0",
    },
    noMarginTable: {
        margin: "0 1em",
        width: "100%"
    },
    withMarginTable: {
        margin: "1.5em 1em",
        width: "100%"
    }
}

const headDescStyle = {
    fontSize: "0.85em",
    margin: "1em 0",
    textAlign: "center"
}
const headTitleStyle = {
    padding: "1.5em 0 0.5em"
}

const disclaimerStyle = {
    fontSize: "0.95em",
    margin: "2em 1em",
}

const cDescMargin = {
    margin: "5px 0px",
    display: "block"
}

const cHeadMargin = {
    margin: "25px 0 15px",
    display: "block",
    // textAlign: "center",
}

const rendererContainer = {
    margin: "1.5em 0 1.5em",
    padding: "1em 0 2em"
}

const headerP = {
    fontSize: "11px",
    margin: "2px 0 0"
}

export interface RendererComponentProps  {
    renderProposal?: any
    renderContract?: any[]
}

export const RendererComponent: React.FC<RendererComponentProps> = (props: RendererComponentProps) => {
        const { renderProposal, renderContract } = props;

        let countTable: number = 0;
        
        renderProposal !== undefined && renderProposal !== 'headerProposal' && renderProposal.map(d => {
            if (renderProposal.length > 1 && d.render == "TABLE") {
                countTable += 1;
            }
        })
        console.log(countTable)
        console.log(renderProposal, renderContract)
        return (
            <>
            <div className={`rendererContainer`} style={rendererContainer}>
            { 
            renderProposal !== undefined && renderProposal !== 'headerProposal' ? 
            renderProposal.map(rp => (
            rp.render == "TABLE" &&
            <div style={{ paddingLeft: "1.5em" }}>
            <table style={tableStyle} id="renderTable">
                <thead>
                        {
                            rp.title.length > 0 && rp.title.map(d => (
                                <tr>
                                    {
                                        d.columns.map((e) => (
                                            <th style={Object.assign(thStyle)} colSpan={ d.columns.length > 1 ? 1 : 2 } className={  d.columns.length == 1 ? 'align-left' : 'align-center' }>
                                                {
                                                e.description.length > 1  ? e.description.map((desc, i) => (
                                                    e.description.length - 1 == i ? 
                                                    <div style={globals.borderBottomNone}>
                                                        <div style={{ margin: "5px" }}>
                                                            <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : desc }}/>
                                                        </div>
                                                    </div> :  
                                                    <div style={Object.assign(spanStyle)}>
                                                        <div style={{ margin: "5px" }}>
                                                            <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : desc }}/>
                                                        </div>
                                                    </div>
                                                )) : 
                                                <div style={globals.borderBottomNone}>
                                                    <div style={{ margin: "5px" }}>
                                                        <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : e.description[0] }}/>
                                                    </div>
                                                </div>
                                                }
                                            </th>
                                        ))
                                    }
                                </tr> 
                            ))
                        }
                </thead>
                <tbody>
                        {
                            rp.body.length > 0 && rp.body.map(d => (
                                <tr>
                                    {
                                        d.columns.length > 0 && d.columns.map((e) => (
                                            <td style={Object.assign(tdStyle )} colSpan={ d.columns.length > 1 ? 1 : 2 } className={ d.columns.length == 1 ? 'align-left' : 'align-center' }>
                                                {
                                                e.description.length > 1  ? e.description.map((desc, i) => (
                                                    e.description.length - 1 == i ? 
                                                    <div style={globals.borderBottomNone}>
                                                        <div style={{ margin: "5px" }}>
                                                            <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : desc }}/>
                                                        </div>
                                                    </div> :  
                                                    <div style={Object.assign(spanStyle)}>
                                                        <div style={{ margin: "5px" }}>
                                                            <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : desc }}/>
                                                        </div>
                                                    </div>
                                                )) : 
                                                <div style={globals.borderBottomNone}>
                                                    <div style={{ margin: "5px" }}>
                                                        <span style={globals.removeAllMargin} dangerouslySetInnerHTML={{ __html : e.description[0] }}/>
                                                    </div>
                                                </div>
                                                }
                                            </td>
                                        ))
                                    }
                                </tr> 
                            ))
                        }
                    </tbody>
            </table>
            </div>
            
            )) :          
                renderProposal == 'headerProposal' &&
                <div>
                    <table className={'header-proposal'} style={{margin: "0 0 5px 0", display: "table", width: "100%", height: "100%"}}>
                        <tr>
                            <td className={'header-logo'} style={{display:"table-cell", width:"50%", verticalAlign: "bottom"}}>
                                <img src={logo} width={"225"} height={"68"}/>
                            </td>
                            <td className={'header-address'} style={{display:"table-cell", width:"50%", verticalAlign: "bottom", textAlign:"right"}}>
                                <p style={headerP}>7th/ F FELIZA Building</p>
                                <p style={headerP}>VA Rufino Street Legaspi Village, Makati City</p>
                                <p style={headerP}>Trunkline: 789-4000</p>
                                <p style={headerP}>Fax No.: 894-4642</p>
                            </td>
                        </tr>
                    </table>
                    <div style={{width:"100%", height: "3px", backgroundColor:"#799328", margin: "2px 0 0 0"}} > </div> 
                    <div style={{width:"100%", height: "3px", backgroundColor:"#6d6e70", margin: "2px 0 0 0"}} > </div> 
                    <h3 className={'proposal-title'} style={{textAlign: "center", margin: "50px 0 35px 0",textTransform: "uppercase"}}>STANDARD PROPOSAL</h3>
                </div>           
            }
            {
                renderProposal !== undefined && renderProposal !== 'headerProposal' && renderProposal.map(rp => (
                    rp.render == "HEADER" &&
                        rp.title.map(d => (
                            <h4 style={headTitleStyle}>{d}</h4>
                        ))
                    ))
            }
            {
                renderProposal !== undefined && renderProposal !== 'headerProposal' && renderProposal.map(rp => (
                    rp.render == "HEADER" &&
                        rp.body.map(d => (
                            d.columns.map(c => (
                                c.description.map(desc => (
                                    <p style={Object.assign(headDescStyle)} className={'align-center'}>
                                        { desc }
                                    </p>
                                ))
                            ))
                        )) 
                    ))
            }
            {
                renderProposal !== undefined && renderProposal !== 'headerProposal' && renderProposal.map(rp => (
                    rp.render == "DISCLAIMER" &&
                        rp.body.map(d => (
                            d.columns.map(c => (
                                c.description.map(desc => (
                                    <p style={Object.assign(disclaimerStyle)} className={'align-center'}>
                                        { desc }
                                    </p>
                                ))
                            ))
                        )) 
                    ))
            }
            {
                renderContract !== undefined && 
                renderContract.map(rc => (
                    rc.render == 'TABLE' &&
                    rc.title.map(t => (
                        t.columns.map(c => (
                            c.description.map(d => (
                                <h4 style={cHeadMargin} dangerouslySetInnerHTML={{ __html : d }}/>
                            ))
                        ))
                    ))
                ))
            }
            {
                renderContract !== undefined && 
                renderContract.map(rc => (
                    rc.render == 'TABLE' &&
                    rc.body.map(b => (
                        b.columns.map(c => (
                            c.description.map(d => (
                                <div className={'renderContract-lists'}>
                                    <span style={cDescMargin} dangerouslySetInnerHTML={{ __html : d }}/>
                                </div>
                            ))
                        ))
                    ))
                ))
            }
            {/* {
                renderContract !== undefined && 
                <footer>
                    <i><small>Please note that the benefit plan / package stated herein are subject to change until the final conforme has been signed and agreed upon.</small></i>
                </footer>
            } */}
            </div>
           </>
        ) 
}     

export default RendererComponent;